import PhoneIcon from '@mui/icons-material/Phone';
import EmailIcon from '@mui/icons-material/Email';
import {ContactForm, ContactItem} from "../components";

const Contact = () => {
    return (<div className="bg-[#1D2336] h-screen w-full pt-[100px] flex lg:flex-row md:flex-row sm:flex-col flex-col">
        <div className="w-[60%]">
            <div className="flex flex-col gap-5 items-center justify-center h-full">
                <ContactItem Icon={PhoneIcon} text="(+216) 71 000 000"/>
                <ContactItem Icon={EmailIcon} text="contact@esprit-ras-robots.tn"/>
            </div>
        </div>
        <div
            className="w-[40%] lg:px-16 md:px-8 sm:px-8 px-8 py-5 flex flex-col justify-center items-center">
            <ContactForm/>
        </div>
    </div>)
}

export default Contact
