const Description = ({title, children, Logo}) => {
    return (<div
            className="flex lg:flex-row md:flex-col sm:flex-col flex-col justify-center lg:px-20 md:px-10 sm:px-10 px-10 lg:gap-0 md:gap-10 sm:gap-10 gap-10">
            <div
                className="text-[20px] text-white text-justify  lg:w-[55%] md:w-[100%] sm:md:w-[100%] md:w-[100%]  flex flex-col gap-5">
                <h2 className="text-5xl font-bold text-left">{title}</h2>
                <p>
                    {children}
                </p>
            </div>
            <div className="lg:w-[55%] md:w-[100%] sm:md:w-[100%] md:w-[100%] flex justify-center items-center">
                <img src={Logo} alt="IEEE Logo" className="w-[300px]"/>
            </div>
        </div>)
}

export default Description;
