import "./style.css"
import {useEffect, useState} from "react";
import {useNavigate} from "react-router-dom";
import {TypeAnimation} from 'react-type-animation';

const Home = () => {
    const navigate = useNavigate();
    const calculateTimeLeft = () => {
        let year = new Date().getFullYear();
        let difference = +new Date(`04/15/${year}`) - +new Date();

        let timeLeft = {};

        if (difference > 0) {
            timeLeft = {
                days: Math.floor(difference / (1000 * 60 * 60 * 24)),
                hours: Math.floor((difference / (1000 * 60 * 60)) % 24),
                minutes: Math.floor((difference / 1000 / 60) % 60),
                seconds: Math.floor((difference / 1000) % 60)
            };
        }

        return timeLeft;
    }


    const [timeLeft, setTimeLeft] = useState(calculateTimeLeft());

    useEffect(() => {
        setTimeout(() => {
            setTimeLeft(calculateTimeLeft());
        }, 1000);


    });

    return (<>
        <div
            className="home-background flex h-screen w-screen justify-center items-center flex-col px-[30px] gap-[50px]">
            <div className="overlay"></div>
            <div className="text-white font-bold text-center">
                <h1 className="md:text-7xl sm:text-4xl text-3xl text-[#c2ae64]">Welcome To</h1>
                {/*<h1 className="md:text-7xl sm:text-4xl text-3xl">ESPRIT RAS ROBOTS 1.0</h1>*/}
                <h1 className="md:text-7xl sm:text-4xl text-3xl">
                    <TypeAnimation
                        sequence={['ESPRIT RAS ROBOTS 1.0',
                            3000,
                            'IEEE RAS ESPRIT SBC',
                            3000,
                           ]}
                        wrapper="span"
                        cursor={true}
                        repeat={Infinity}
                    />
                </h1>
            </div>
            <div className="text-white text-[25px] font-light text-center">
                <div className="flex flex-row gap-5">
                    <div>{timeLeft.days} Days</div>
                    <div>{timeLeft.hours} Hours</div>
                    <div>{timeLeft.minutes} Minutes</div>
                    <div>{timeLeft.seconds} Seconds</div>
                </div>

            </div>
            <div className="flex lg:flex-row md:flex-row sm:flex-col flex-col lg:gap-5 md:gap-5">
                <button className="border-2 border-secondary rounded-xl text-white font-bold text-[20px] mt-[20px] px-8 py-3 hover:bg-secondary" onClick={() => navigate("/Register")}>Register Your Team</button>
                <button className="border-2 border-lightblue rounded-xl text-white font-bold text-[20px] mt-[20px] px-8 py-3 hover:bg-lightblue" onClick={() => navigate("/Register")}>Mockup</button>
            </div>
        </div>
        <div>

        </div>
    </>)
}

export default Home;
