import IEEE_Logo from "../assets/images/IEEE_Logo.png"
import Logo_SB from "../assets/images/Logo_SB.png"
import Logo_RAS from "../assets/images/Logo RAS.png"
import {Description, Stats, StatsItem} from "../components";

const IEEE = () => {
    return (<div className="pt-[150px] h-full w-full bg-[#1D2336] flex justify-center">
        <div className="flex flex-col gap-28">
            <Description title="IEEE" Logo={IEEE_Logo}>
                Born on january 1st 1963, it is a non-profit technical professional association. Since its
                inception, IEEE has advanced the theory and application of technology and science, served as a
                catalyst for technological innovation, and supported the needs of its members through a wide
                variety
                of programs and services. Through its membership, the IEEE is a leading authority in a variety
                of
                technical fields.
            </Description>

            <Stats>
                <StatsItem title="Countries" number={160}/>
                <StatsItem title="Members" number="432K"/>
                <StatsItem title="Student Members" number="125K"/>
                <StatsItem title="Researchers Members" number="307K"/>
            </Stats>

            <Description title="IEEE ESPRIT Student Branch" Logo={Logo_SB}>
                IEEE ESPRIT SB is the largest student branch in Tunisia and in
                the region 8 ( Europe, Africa et Middle east ) as well as the 4th
                in the world with more than 500 members.
            </Description>
            <Stats>
                <StatsItem number="450" title="Members"/>
                <StatsItem number="200" title="Congress, Workshops, Competitions"/>
                <StatsItem number="19" title="Awards"/>
                <StatsItem number="10" title="8 Technical Chapters" title1="2 Affinity Groups"/>
            </Stats>

            <Description title="IEEE RAS ESPRIT SBC" Logo={Logo_RAS}>
                The IEEE RAS (Robotics and Automation Society) Chapter is a professional organization affiliated
                with the Institute of Electrical and Electronics Engineers (IEEE) that focuses on the
                advancement of robotics and automation technologies. The IEEE RAS Chapter provides a platform
                for researchers, engineers, and professionals from academia, industry, and government to
                exchange knowledge, share ideas, and collaborate on projects related to robotics and automation.
                <br/>
                <br/>
                Our activities as IEEE ESPRIT RAS Chapter, are to deepen knowledge in automation and robotics by
                organizing weekly workshops, participating in and organizing national competitions, and
                preparing members for professional life by integrating them into a network of young
                professionals and engineers.
            </Description>

            <Stats>
                <StatsItem number="75" title="Members"/>
                <StatsItem number="40" title="Congress, Workshops, Competitions"/>
                <StatsItem number="2018" title="Foundation"/>
            </Stats>


        </div>

    </div>)
}

export default IEEE
