import {PlanningItem} from "../components";

const Planning = () => {
    return (<div
        className="bg-[#1D2336] h-full w-full lg:px-[120px] md:px-[100px] sm:px-[20px] px-[20px] py-[120px] flex flex-col gap-10 items-center">
        <h1 className="font-bold text-[50px] text-white">Planning</h1>
        <div
            className="flex flex-col gap-5 lg:w-[70%] md:w-[80%] sm:w-[100%] w-[100%] lg:text-[20px] md:text-[15px] sm:text-[15px] text-[15px]">
            <PlanningItem title="IFTAR" dateTime="15 April - 6.50 PM -> 8 PM" color="white"/>
            <PlanningItem title="Welcoming" dateTime="15 April - 8 PM -> 8.30 PM" color="secondary"/>
            <PlanningItem title="Line Follower Approval (Homologation)" dateTime="15 April - 8.30 PM -> 10 PM"
                          color="white"/>
            <PlanningItem title="Line Follower (1st attempt) / JUNIOR" dateTime="15 April - 10 PM -> 12 AM"
                          color="secondary"/>
            <PlanningItem title="Junior Winner Announcements" dateTime="16 April - 12 AM -> 12.30 AM" color="white"/>
            <PlanningItem title="Line Follower (2nd attempt)" dateTime="16 April - 12.30 AM -> 2.45 AM"
                          color="secondary"/>
            <PlanningItem title="Line Follower Winner Announcements" dateTime="16 April - 2.45 AM" color="white"/>
            <PlanningItem title="All Roads Approval (Homologation)" dateTime="16 April - 2.45 AM -> 3.15 AM"
                          color="secondary"/>
            <PlanningItem title="Suhoor" dateTime="16 April - 3.15 AM -> 4.15 AM" color="white"/>
            <PlanningItem title="All Roads Approval (Homologation)" dateTime="16 April - 4.15 AM -> 5.15 AM"
                          color="secondary"/>
            <PlanningItem title="Fighter Approval (Homologation) / All Roads" dateTime="16 April - 5.15 AM -> 7.15 AM"
                          color="white"/>
            <PlanningItem title="All Roads Winners Announcement" dateTime="16 April - 7.15 AM -> 7.30 AM"
                          color="secondary"/>
            <PlanningItem title="Fighter" dateTime="16 April - 7.30 AM -> 9.30 AM" color="white"/>
            <PlanningItem title="Fighter Winner Announcements" dateTime="16 April - 9.30 AM" color="secondary"/>
            <PlanningItem title="Closing Ceremony" dateTime="16 April - 10 AM" color="white"/>

        </div>

    </div>)
}

export default Planning
