import {Link, useNavigate} from "react-router-dom";
import useMedia from "../customHooks/useMedia";
import {useState} from "react";

//Import Icons
import MenuIcon from '@mui/icons-material/Menu';
import CloseIcon from '@mui/icons-material/Close';

//Import Images
import Logo from '../assets/images/Logo.png';


const nav_items = ["Home", "IEEE", "Scoreboard", "Planning", "Sponsors", "Contact"]

const Navbar = ({active}) => {
    const isSmallDevice = useMedia('(max-width: 1100px)');
    const [isMenuOpen, setIsMenuOpen] = useState(false);
    const navigate = useNavigate();

    if (active === "") {
        active = "Home"
    }

    return (isSmallDevice ? (<>
        <nav
            className="bg-dark h-[100px] flex flex-row justify-between items-center p-[20px] drop-shadow-lg fixed w-screen">
            <img className="h-[50px]" src={Logo} alt="" onClick={() => navigate("/")}/>
            {!isMenuOpen && <MenuIcon className="text-white" sx={{fontSize: 40}} onClick={() => setIsMenuOpen(true)}/>}
        </nav>
        {isMenuOpen && (<div
            className="absolute top-0 left-0 w-full h-full bg-dark bg-opacity-80 flex justify-center items-center">
            <ul className="flex flex-col justify-between items-center gap-[30px] font-semibold font-[20px] text-white">
                <CloseIcon className="absolute right-5 top-8 text-white" sx={{fontSize: 40}} onClick={() => setIsMenuOpen(false)}/>

                {nav_items.map((item, index) => {
                    return (
                        <li className={active === item ? "underline underline-offset-8 decoration-[#c2ae64] decoration-[3px]" : undefined}
                            key={index}><Link
                            to={item === "Home" ? "/" : `/${item}`}>{item} </Link></li>)
                })}
            </ul>
        </div>)}


    </>) : (<nav
            className="bg-dark h-[100px] flex flex-row justify-between items-center py-[40px] px-[100px] fixed w-screen drop-shadow-lg z-10">
            <img className="h-[50px] cursor-pointer" src={Logo} alt="" onClick={() => navigate("/")}/>
            <ul className="flex flex-row justify-between items-center gap-[30px] font-[20px] text-white font-semibold">
                {nav_items.map((item, index) => {
                    return (
                        <li className={active === item ? "underline underline-offset-8 decoration-[#c2ae64] decoration-[3px]" : undefined}
                            key={index}><Link
                            to={item === "Home" ? "/" : `/${item}`}>{item} </Link></li>)
                })}
            </ul>
            <button className="bg-secondary hover:bg-[#8d7500] rounded-3xl font-bold font-[20px] text-white py-3 px-10"
                    onClick={() => navigate("/Register")}> Register
            </button>
        </nav>

    ));
}


export default Navbar;
