const StatsItem = ({title,title1, number}) => {
    return(
        <div className="flex flex-col text-center text-xl font-bold ">
            <h3 className="text-3xl">{number}</h3>
            <h3>{title}</h3>
            <h3>{title1}</h3>
        </div>
    )
}

export default StatsItem
