import {useEffect, useState} from "react";
import {isEmail, isRealName} from "../utils/formChecker";
import axios from "axios";
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import {Button} from "@mui/material";
import {useNavigate} from "react-router-dom";
import Lottie from 'react-lottie';
import * as Loader from "../assets/images/loader.json";
import {InputText} from "../components";

const Register = () => {
    const navigate = useNavigate();

    const [loading, setLoading] = useState(true);

    const [teamName, setTeamName] = useState("");
    const [teamCompetition, setTeamCompetition] = useState("");

    const [teamNameError, setTeamNameError] = useState("");
    const [teamCompetitionError, setTeamCompetitionError] = useState("");

    const [teamLeaderName, setTeamLeaderName] = useState("");
    const [teamLeaderEmail, setTeamLeaderEmail] = useState("");
    const [teamLeaderPhone, setTeamLeaderPhone] = useState("");

    const [teamLeaderNameError, setTeamLeaderNameError] = useState("");
    const [teamLeaderEmailError, setTeamLeaderEmailError] = useState("");
    const [teamLeaderPhoneError, setTeamLeaderPhoneError] = useState("");

    const [teamMember1Name, setTeamMember1Name] = useState("");
    const [teamMember1Email, setTeamMember1Email] = useState("");
    const [teamMember1Phone, setTeamMember1Phone] = useState("");

    const [teamMember1NameError, setTeamMember1NameError] = useState("");
    const [teamMember1EmailError, setTeamMember1EmailError] = useState("");
    const [teamMember1PhoneError, setTeamMember1PhoneError] = useState("");

    const [teamMember2Name, setTeamMember2Name] = useState("");
    const [teamMember2Email, setTeamMember2Email] = useState("");
    const [teamMember2Phone, setTeamMember2Phone] = useState("");

    const [teamMember2NameError, setTeamMember2NameError] = useState("");
    const [teamMember2EmailError, setTeamMember2EmailError] = useState("");
    const [teamMember2PhoneError, setTeamMember2PhoneError] = useState("");

    const [error, setError] = useState("");

    const [open, setOpen] = useState(false);

    const submitForm = async () => {
        let isValid = true;
        if (teamName === "") {
            setTeamNameError("Team Name is required");
            isValid = false;
        }

        if (teamCompetition === "") {
            setTeamCompetitionError("Team Competition is required");
            isValid = false;
        }

        if (teamLeaderName === "") {
            setTeamLeaderNameError("Team Leader Name is required");
            isValid = false;
        } else if (!isRealName(teamLeaderName)) {
            setTeamLeaderNameError("Team Leader Name is not valid");
            isValid = false;
        }

        if (teamLeaderEmail === "") {
            setTeamLeaderEmailError("Team Leader Email is required");
            isValid = false;
        } else if (!isEmail(teamLeaderEmail)) {
            setTeamLeaderEmailError("Team Leader Email is not valid");
            isValid = false;
        }

        if (teamLeaderPhone === "") {
            setTeamLeaderPhoneError("Team Leader Phone is required");
            isValid = false;
        }

        if (teamMember1Email !== "" || teamMember1Name !== "" || teamMember1Phone !== "") {
            if (teamMember1Email === "") {
                setTeamMember1EmailError("Team Member 1 Email is required");
                isValid = false;
            } else if (!isEmail(teamMember1Email)) {
                setTeamMember1EmailError("Team Member 1 Email is not valid");
                isValid = false;
            }

            if (teamMember1Name === "") {
                setTeamMember1NameError("Team Member 1 Name is required");
                isValid = false;
            } else if (!isRealName(teamMember1Name)) {
                setTeamMember1NameError("Team Member 1 Name is not valid");
                isValid = false;
            }

            if (teamMember1Phone === "") {
                setTeamMember1PhoneError("Team Member 1 Phone is required");
                isValid = false;
            }
        }

        if (teamMember2Email !== "" || teamMember2Name !== "" || teamMember2Phone !== "") {
            if (teamMember2Email === "") {
                setTeamMember2EmailError("Team Member 2 Email is required");
                isValid = false;
            } else if (!isEmail(teamMember2Email)) {
                setTeamMember2EmailError("Team Member 2 Email is not valid");
                isValid = false;
            }

            if (teamMember2Name === "") {
                setTeamMember2NameError("Team Member 2 Name is required");
                isValid = false;
            } else if (!isRealName(teamMember2Name)) {
                setTeamMember2NameError("Team Member 2 Name is not valid");
                isValid = false;
            }

            if (teamMember2Phone === "") {
                setTeamMember2PhoneError("Team Member 2 Phone is required");
                isValid = false;
            }
        }

        if (!isValid) return setError("Please fill all the required fields");

        const teamMembers = [];

        teamMembers.push({
            name: teamLeaderName, email: teamLeaderEmail, phone: teamLeaderPhone
        })

        if (teamMember1Name) teamMembers.push({
            name: teamMember1Name, email: teamMember1Email, phone: teamMember1Phone
        });

        if (teamMember2Name) teamMembers.push({
            name: teamMember2Name, email: teamMember2Email, phone: teamMember2Phone
        });


        if (isValid) {
            setLoading(true);
            try {
                const res = await axios.post("http://209.133.196.106:7080/api/team", {
                    email: teamLeaderEmail, name: teamName, competition: teamCompetition, teamMembers: teamMembers
                })
                if (!res.status > 200) return;
                setLoading(false);
                setOpen(true);
            } catch (err) {
                setLoading(false);
                if (!err?.response) return setError("Something went wrong, please try again later");
                if (err?.response?.data?.error) return setError(err?.response?.data?.error);

            }
        }
    }

    const resetErrors = () => {
        setTeamNameError("");
        setTeamLeaderNameError("");
        setTeamLeaderEmailError("");
        setTeamLeaderPhoneError("");
        setTeamMember1EmailError("");
        setTeamMember1NameError("");
        setTeamMember1PhoneError("");
        setTeamMember2EmailError("");
        setTeamMember2NameError("");
        setTeamMember2PhoneError("");
        setError("");
    }


    useEffect(() => {
        resetErrors();
    }, [teamName, teamLeaderName, teamLeaderEmail, teamLeaderPhone, teamMember1Email, teamMember1Name, teamMember1Phone, teamMember2Email, teamMember2Name, teamMember2Phone]);

    useEffect(() => {
        setLoading(false);
    }, []);


    const defaultOptions = {
        loop: true, autoplay: true, animationData: Loader, rendererSettings: {
            preserveAspectRatio: 'xMidYMid slice'
        }
    };

    if (loading) return (<div className="pt-[100px] bg-[#0F1527] h-screen">
        <Lottie options={defaultOptions}
                height={400}
                width={400}/>
    </div>)

    return (<div className="h-full w-full bg-[#0F1527] flex flex-row justify-center pt-[100px] items-center">
        <Dialog
            open={open}
            onClose={() => setOpen(false)}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
        >
            <DialogTitle id="alert-dialog-title">
                <div className="font-bold"> Team Created Successfully</div>
            </DialogTitle>
            <DialogContent>
                <DialogContentText id="alert-dialog-description">
                    Team created successfully! You have been added to the team as a team leader <br/><br/>
                    Please note the following:<br/> <br/>
                    1- You can only lead one team at a time<br/>
                    2- If you want to delete your team or make any changes, please send an email to <a
                    className="text-blue-800"
                    href="mailto:mohamedhabiballah.bibani@esprit.tn"> mohamedhabiballah.bibani@esprit.tn </a> with your
                    team name and we will assist you.<br/><br/>
                    You will now be redirected to the home page. If you have any questions, please don't hesitate to
                    contact us at the email address above.

                </DialogContentText>
            </DialogContent>
            <DialogActions>
                <Button onClick={() => navigate("/")} autoFocus>
                    OK
                </Button>
            </DialogActions>
        </Dialog>
        <div className="md:w-[45%] sm:w-[80%] w-[90%] ">
            <div
                className="flex flex-col bg-dark justify-center items-center gap-[45px] rounded-xl py-[40px] my-[40px]">
                <div className="font-bold text-[25px] text-white text-center">Register Your Team!</div>
                <div className="flex flex-col gap-[40px] w-full justify-center items-center ">
                    {/*//Team Name*/}
                    <div className="flex flex-col gap-[20px] w-[80%]">
                        <InputText placeholder="Team Name" value={teamName}
                                   onChange={(e) => setTeamName(e.target.value)} error={teamNameError}/>

                        <div className="flex flex-col w-[100%]">
                            {teamCompetitionError && (<div
                                className="text-red-800 font-light text-[14px] mt-[-10px] ml-[5px]">{teamCompetitionError}</div>)}
                            <select
                                className="bg-primary p-4 outline-0 rounded-lg text-white font-light text[15px]"
                                value={teamCompetition}
                                onChange={(e) => setTeamCompetition(e.target.value)}>
                                <option value="">Select a challenge</option>
                                <option value="OFF_ROAD">All Roads</option>
                                <option value="LINE_FOLLOWER">Line Follower</option>
                                <option value="JUNIOR">Junior</option>
                                <option value="FIGHTER">Fighter</option>
                            </select>
                        </div>
                    </div>
                    {/*Team Leader*/}
                    <div className="flex flex-col gap-[20px] w-[80%]">
                        <InputText placeholder="Team Leader Name" value={teamLeaderName}
                                   onChange={(e) => setTeamLeaderName(e.target.value)} error={teamLeaderNameError}/>

                        <div className="flex md:flex-row sm:flex-col flex-col gap-5 w-[100%]">
                            <InputText placeholder="Team Leader Email" value={teamLeaderEmail}
                                       onChange={(e) => setTeamLeaderEmail(e.target.value)}
                                       error={teamLeaderEmailError}/>
                            <InputText placeholder="Team Leader Phone" value={teamLeaderPhone}
                                       onChange={(e) => setTeamLeaderPhone(e.target.value)}
                                       error={teamLeaderPhoneError}/>
                        </div>
                    </div>
                    {/*Team Member 1*/}
                    <div className="flex flex-col gap-[20px]  w-[80%]">
                        <InputText placeholder="Team Member 1 Name" value={teamMember1Name}
                                   onChange={(e) => setTeamMember1Name(e.target.value)} error={teamMember1NameError}/>

                        <div className="flex md:flex-row sm:flex-col flex-col gap-5 w-[100%]">
                            <InputText placeholder="Team Member 1 Email" value={teamMember1Email}
                                       onChange={(e) => setTeamMember1Email(e.target.value)}
                                       error={teamMember1EmailError}/>
                            <InputText placeholder="Team Member 1 Phone" value={teamMember1Phone}
                                       onChange={(e) => setTeamMember1Phone(e.target.value)}
                                       error={teamMember1PhoneError}/>
                        </div>
                    </div>
                    {/*Team Member 2*/}
                    <div className="flex flex-col gap-[20px] w-[80%]">
                        <InputText placeholder="Team Member 2 Name" value={teamMember2Name}
                                   onChange={(e) => setTeamMember2Name(e.target.value)} error={teamMember2NameError}/>
                        <div className="flex md:flex-row sm:flex-col flex-col gap-5 w-[100%]">
                            <InputText placeholder="Team Member 2 Email" value={teamMember2Email}
                                       onChange={(e) => setTeamMember2Email(e.target.value)}
                                       error={teamMember2EmailError}/>
                            <InputText placeholder="Team Member 2 Phone" value={teamMember2Phone}
                                       onChange={(e) => setTeamMember2Phone(e.target.value)}
                                       error={teamMember2PhoneError}/>

                        </div>
                    </div>
                </div>
                {error && <div className="text-red-800 font-light text-[14px] mt-[-10px] ml-[5px]">{error}</div>}
                <button
                    className="px-[50px] py-[10px] rounded-2xl border-2 border-[#c2ae64] text-white hover:bg-[#c2ae64] text-[17px] font-bold"
                    onClick={submitForm}>Register
                </button>
            </div>
        </div>
    </div>)
}

export default Register
