const InputText = ({placeholder, value, onChange, error}) => {
    return (
        <div className={"flex flex-col w-[100%]"}>
            {error && (<div className="text-red-800 font-light text-[14px] mt-[-10px] ml-[5px]">{error}</div>)}
            <input type="text" placeholder={placeholder}
                   className="bg-primary p-4 outline-0 rounded-lg text-white font-light text[15px]" value={value}
                   onChange={onChange}/>
        </div>
    )
}

export default InputText
