const isEmail = (email) => {
    return email.indexOf('@') > -1;
}

const containsNumbers = (str) => {
    return /\d/.test(str);
}


const containsLetters = (str) => {
    const regex = /[a-zA-Z]/;
    return regex.test(str);
}

const isRealName = (name) => {
    return !containsNumbers(name) && name.length > 2;
}


export {isEmail, isRealName, containsLetters, containsNumbers}
